.to-do-list {
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  margin-top: auto;
  
}
h1 {
  font-size: 4rem;
}
body {
  background-image: url(NP.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
button {
  font-size: 1rem;
  font-weight: bold;
  padding: 10px 10px;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;

}
.add-button {
  background-color: rgb(51, 226, 51);
}
.add-button:hover {
  background-color: rgb(6, 161, 6);
}
.delete-button {
  background-color: rgb(216, 17, 17);
}
.delete-button:hover {
  background-color: rgb(187, 13, 13);
}
.up-button, .down-button {
  background-color: rgb(115, 172, 247);
}
.up-button, .down-button, .delete-button {
  margin-left: 10px;
}
input[type="text"] {
  font-size: 1rem;
  padding: 10px;
  border: none;
  border-radius: 10px;
}
ol {
  padding: 0;
  
}
li {
  font-size: 1.6rem;
  font-weight: bold;
  padding: 15px;
  align-items: center;
  display: flex;
  
}
.text {
  flex: 1;
}